
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.today-task {

  .ant-tag {
    margin-bottom: 10px;
  }

  .task-component {
    &:not(:nth-last-child(1)) {
      margin-bottom: 15px;
    }
  }

  .wrapper {
    flex: 1;
    overflow: hidden;
    overflow-y: auto;

    .ant-row-flex {
      height: 100%;

      .ant-col:not(:nth-last-child(1)) {
        border-right: 1px dashed #dcd5d5;
      }
    }
  }
}
