
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.memorandum {

  .button-group {
    margin-top: 15px;
    text-align: right;

    .ant-btn {
      margin-left: 10px;
    }
  }

  .ant-card-body {
    padding: 10px 20px;
  }
  
  .ant-col {
    margin-bottom: 15px;
  }

  .ant-card-body,
  .ant-card-head-title,
  .content {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .content {
    margin-top: 5px;

    & > * {
      display: inline;
      font-size: 14px;
      font-weight: normal;
      color: #333;
    }
  }
}

