
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.setting-page {
  background: #fff;

  .ant-layout-content {
    padding: 15px 30px !important;
  }
}