
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

$padding: 15px;

.task-component {

  .ant-card-body {
    padding: $padding 0;
  }

  .content {
    padding: 0 24px $padding $padding;
    border-bottom: 1px dashed #ccc;
    white-space: pre;
  }

  .level {
    padding-left: $padding;
  }
  
  .button-wrapper {
    padding: 0 $padding;
    display: flex;
    justify-content: flex-end;

    .ant-btn:not(:nth-last-child(1)) {
      margin-right: 7px;
    }
  }
}