
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.capital-flow-type {
  background: #fff;
  
  .button-group {
    padding: 10px 20px;

    .ant-btn {
      margin-right: 10px;
    }
  }

  .ant-table-wrapper {
    overflow-y: auto;
  }
}