
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.editor-page {

  .input-title {
    font-weight: bold;
  }

  #edit-section {
    margin: 20px 0;
    flex: 1;
    background: #fff;
  }

  .button-group {
    text-align: right;

    .ant-btn {
      margin-left: 15px;
    }
  }
}
