
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.memorandum-detail {
  position: relative;
  padding: 20px;
  background: #fff;
  overflow-y: auto !important;

  .title {
    font-size: 36px;
    color: #262626;
    border-bottom: 1px solid #e6e6e6;
  }

  .edit {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 16px;
    color: inherit;
  }
}
