
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.system-data {

  .ant-card {
    height: 250px;
    margin-bottom: 25px;
  }
  
  .item-text {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    &:nth-last-child(1) {
      margin-bottom: 0;
    }

    em {
      color: #000;
      font-weight: 500;
    }
  }

  .mem .ant-card-body {
    text-align: center;

    .surplus {
      margin-top: 10px;
    }
  }
}