
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ant-layout-header {
  z-index: 1;
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
  padding-right: 20px;
  background: #fff;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2);

  .left {
    float: left;
  }

  a {
    display: block;
    color: inherit;
  }

  .right {
    float: right;
    overflow: hidden;

    li {
      float: left;
      height: 100%;
      cursor: pointer;
      padding: 0 10px;
      
      &:hover {
        background: #f7f7f7;
        color: #333;
      }

      i {
        vertical-align: middle;
        font-size: 18px;
      }
    }

    .username {
      margin-left: 5px;
      vertical-align: middle;
    }
  }
}

.popover-content {
  width: 200px;

  .ls {
    display: block;
    padding: 8px 15px;
    border-bottom: 1px solid #f7f7f7;
    cursor: pointer;
    color: inherit;

    &:hover {
      background: #f5f5f5 !important;
      color: #595959;
    }
  }

  .sign-out {
    background: #fafafa;
    text-align: center;
  }
}

.message-popover {
  width: 330px;
  font-size: 12px;

  .msg-header {
    display: flex;
    background: #f7f7f7;

    .left {
      flex: 1;
      font-size: 14px;
    }

    .right {
      cursor: pointer;
      color: $theme-color;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .item-block {
    padding: 10px 20px;

    .content {
      @include ellipsis;
    }
  }

  .ls {
    display: block;
    cursor: pointer;
    border-bottom: 1px solid #eee;
    color: inherit;

    &:hover {
      background: #f7f7f7;
    }
  }
}
