
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.login-page {
  display: flex;
  min-height: 100vh;
  background: #f0f2f5 url("~@/assets/img/public/bg.svg") no-repeat center 110px;
  background-size: 100%;
  flex-direction: column;

  .wrap {
    flex: 1;
    margin-bottom: 150px;
    width: 320px;
    align-self: center;
    @include flex-center;
  }

  .ant-input-affix-wrapper:not(:nth-child(1)) {
    margin-top: 20px;
  }

  .anticon {
    color: #0000004d;
  }

  .logo-wrap {
    margin-bottom: 20px;
    text-align: center;

    .logo {
      width: 80px;
      -webkit-user-drag: none;
      border-radius: 50%;
    }

    em {
      font-size: 24px;
      font-weight: bold;
      color: #000;
      margin-left: 15px;
      vertical-align: middle;
    }
  }

  .captcha {
    display: block;
    cursor: pointer;
    height: 30px;
    border-left: 1px solid #ccc;
  }

  .other-login {
    margin-top: 15px;
    display: flex;

    .txt {
      margin-right: 15px;
      vertical-align: text-bottom;
    }

    i {
      cursor: pointer;
      transition: .1s linear;
      font-size: 22px;
      color: #ccc;

      &:hover {
        color: #000 !important;
      }
    }

    .to-register {
      flex: 1;
      text-align: right;
      color: $theme-color;
    }
  }
}