
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.about {
  padding: 10px;
  background: #fff;

  h2 {
    padding-bottom: 10px;
    border-bottom: 1px solid #e6e6e6;
  }

  a {
    text-decoration: underline;
  }

  .block-item {

    &:not(:nth-last-child(1)) {
      margin-bottom: 15px;
    }
  }

  .suupport {

    .pay-img {
      width: 500px;
    }
  }
}